import axios from 'axios'
import React, { useEffect, useState } from 'react'
import Spinner from '../Spinner/Spinner'
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';


export default function Brands() {
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    let [loading, setloading] = useState(true)
    let [Brands, setBrands] = useState([])
    let [BrandDetails, setBrandDetails] = useState([])
    useEffect(() => {
        getBrands()
    }, [])
    async function getBrands() {
        let req = await axios.get(`https://ecommerce.routemisr.com/api/v1/brands`)
        if (req.status == 200) {
            setBrands(req?.data?.data)
            setloading(false)
        }
    }
    
    async function getSpecificBrands(id) {
        setloading(true)
        let req = await axios.get(`https://ecommerce.routemisr.com/api/v1/brands/${id}`)
        if(req.status == 200){
            setBrandDetails([req?.data?.data])
            setloading(false)
        }
    }

    return (
        <>
            <h2 className='d-flex justify-content-center text-main mb-5 h1'>All Brands</h2>


            <div className="Brands">
                <div className="row g-5">
                    {loading ? <Spinner /> : <>
                        {Brands.map((e) => {
                            return <div className='col-md-3  bg-transparent border-0 cursor-pointer ' onClick={handleShow}>
                                     <div onClick={() => getSpecificBrands(e._id)} className="Brand border border-black position-relative">
                                    <img className='w-100' src={e.image} alt="" />
                                    <p className='d-flex justify-content-center text-dark'>{e.name}</p>
                                </div>
                            </div>
                        })}
                    </>}
                        
                        
                    {BrandDetails.map((e) => {
                        return <>
                        {loading ? <Spinner/>:
                        <Modal show={show} onHide={handleClose}>
                        <Modal.Header closeButton>
                        </Modal.Header>
                        <Modal.Body><div className="row justify-content-between">
                            <div className="col-md-5 my-5 ms-3">
                                <h2 className='text-main h1'>{e.name}</h2>
                                <p>{e.name}</p>
                            </div>
                            <div className="col-md-6">
                                <img className='w-100' src={e.image} alt="" />
                            </div>
                        </div></Modal.Body>
                        <Modal.Footer>
                            <Button variant="secondary" onClick={handleClose}>
                                Close
                            </Button>
                        </Modal.Footer>
                    </Modal>}
                        </>
                    })}
                </div>


            </div>
        </>
    )
}
