import React, { useState } from 'react'
import { useFormik } from 'formik'
import * as Yup from "yup"
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
export default function ForgetPassword() {
    let [errmsg,SetErr] = useState("")
    let navg = useNavigate()
    let [formStatus , setFormStatus] = useState(true)
    let validationSchema = Yup.object({
        email: Yup.string().required("email Required").email("Valid email Required")
    })
    let validationSchema2 = Yup.object({
        resetCode: Yup.string().required("resetCode Required").matches(/^[0-9]{5,6}$/,"enter Valid Code")
    })
    
    let Formik = useFormik({
        initialValues: {
            email: ""
        },
        onSubmit: ForgetPasswordApi,
        validationSchema
    })

    let Formik2 = useFormik ({
        initialValues:{
            resetCode: ''
        },
        onSubmit:verifyResetCode,
        validationSchema:validationSchema2
    })
    async function ForgetPasswordApi(value){
        let req = await axios.post('https://ecommerce.routemisr.com/api/v1/auth/forgotPasswords',value).catch((err)=>{
            SetErr(err.response.data.message)
            console.log(err.response.data.message);
        })
        if(req.data.statusMsg == 'success'){
           setFormStatus(false)
        }
        console.log(req);
    }

    async function verifyResetCode(value){
        let req = await axios.post('https://ecommerce.routemisr.com/api/v1/auth/verifyResetCode',value).catch((err)=>{
            SetErr(err.response.data.message)
            console.log(err.response.data.message);
        })
        if(req.data.status == 'Success'){
            navg("/ResetPassword")
        }
        console.log(req);
    }
  return (
    <div>
        {errmsg ? <div className="alert alert-danger">{errmsg}</div> : ""}

        {formStatus ? 
        <form onSubmit={Formik.handleSubmit}>
        <label htmlFor="email">Enter Your Email</label>
        <input className='form form-control my-2' onBlur={Formik.handleBlur} onChange={Formik.handleChange} type="email" name="email" id="email" />
        <button type='submit' className='btn bg-main text-white mb-3'>Send</button>
      </form>
        :
      <form onSubmit={Formik2.handleSubmit}>
        <label htmlFor="resetCode">Enter reset code</label>
        <input onBlur={Formik2.handleBlur} onChange={Formik2.handleChange} className='form form-control my-2'  type="text" name="resetCode" id="resetCode" />
        {Formik2.errors.resetCode && Formik2.touched.resetCode ? <div className="alert alert-danger">{Formik2.errors.resetCode}</div>:""}
        <button type='submit' className='btn bg-main text-white'>Verify code</button>
      </form>}
    

    </div>
  )
}

