import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import Spinner from '../Spinner/Spinner'
import { useQuery } from 'react-query'
import { Link } from 'react-router-dom'
import MainSlider from '../MainSlider/MainSlider'
import CategorySlider from '../CategorySlider/CategorySlider'
import { CartContext } from '../Context/CartContext'
import Swal from 'sweetalert2'
import { WishContext } from '../WishContext/WishContext'

export default function Home() {
  let {getwishListProducts} = useContext(WishContext)
  let [productids,setproductids] = useState([])
  let [loading,setloading] = useState(true)
  useEffect(()=>{
    getHeart()
  })

  let { PostWish, setWishNum } = useContext(WishContext)
  let { addCart, setItemNumber } = useContext(CartContext)
  let [page, setpage] = useState(1)
  function getProducts(queryData) {
    return axios.get(`https://ecommerce.routemisr.com/api/v1/products?page=${queryData.queryKey[1]}`)
  }
  let { isLoading , isError, isFetching, data } = useQuery(['productsApi', page], getProducts)

  /////
  async function getHeart() {
    let req = await getwishListProducts();
    let reqData = await axios.get(
      "https://ecommerce.routemisr.com/api/v1/products"
    ).catch(()=>{})
      if(req?.data.status == "success"){
        let WishsId = []
        let prosuctsId = []
        data?.data?.data.map((el)=>{
          prosuctsId.push(el._id)
        })
    
        req?.data.data.map((el)=>{
          WishsId.push(el.id)
        })
    
        const commonElements = prosuctsId.filter(element => WishsId.includes(element));
        // console.log("Common elements:", commonElements);
    
      commonElements.forEach(commonElement => {
        const element = document.getElementById(`element_${commonElement}`);
        if (element) {
          element.classList.replace("fa-regular", "fa-solid");
        }
      });
      }


}
/////////

  function getPageNumber(e) {
    let page = e.target.getAttribute('pageNumber')
    setpage(page)
  }
  async function AddToCart(id) {
    let req = await addCart(id).catch((err) => {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: "Something went wrong!",
        footer: '<a href="#">Why do I have this issue?</a>'
      });

    })
    if (req.data.status == 'success') {
      setItemNumber(req.data.numOfCartItems)
      Swal.fire({
        title: "Good job!",
        text: "You clicked the button!",
        icon: "success"
      });

    }
  }


  async function getWishList(e, id) {
    let req = await PostWish(id).catch()
    if (req?.data?.status == "success"){
      setWishNum(req.data.data.length)
    }
      e.target.classList.replace("fa-regular", "fa-solid")
  }
  return (
    <>
      <MainSlider></MainSlider>
      <CategorySlider></CategorySlider>
      {isLoading ?
        <Spinner />
        : <div className='container py-5'>
          <div className="row g-5">
            {data?.data?.data.map((el) => {
              return <div className="col-md-2">
                <div className="product pb-2 position-relative">
                  <Link to={'/productsDetails/' + el.id}>

                    <img src={el.imageCover} className='w-100' alt="" />
                    <h6 className='text-main'>{el.category.name}</h6>
                    <h5>{el.title.split(" ").slice(0, 2).join(" ")}</h5>
                    <div className="d-flex justify-content-between">
                      <span>{el.price}EGP</span>
                      <span><i className='fa-solid fa-star rating-color'></i>{el.ratingsAverage}</span>
                    </div>
                  </Link>

                {isLoading ?<Spinner/> :<i id={'element_'+el._id} onClick={(e) => getWishList(e, el.id)} className='heart fa-regular fa-heart fa-2x position-absolute top-0 end-0 m-2 text-danger'></i>}
                  


                  <button onClick={() => { AddToCart(el.id) }} className='btn bg-main text-white d-block w-100'>Add to Cart</button>
                </div>
              </div>
            })}
          </div>

          <nav aria-label="Page navigation example">
            <ul className="pagination justify-content-center my-5">
              <li className="page-item">
                <a className="page-link" href="#" aria-label="Previous">
                  <span aria-hidden="true">&laquo;</span>
                </a>
              </li>
              <li className="page-item"><a className="page-link" pageNumber='1' onClick={getPageNumber}>1</a></li>
              <li className="page-item"><a className="page-link" pageNumber='2' onClick={getPageNumber}>2</a></li>
              <li className="page-item">
                <a className="page-link" href="#" aria-label="Next">
                  <span aria-hidden="true">&raquo;</span>
                </a>
              </li>
            </ul>
          </nav>

        </div>}



    </>
  )
}
