import axios from "axios";
import { createContext, useState } from "react";

export let CartContext = createContext ()

export function CartContextprovider({children}){
    let [numsItem,setItemNumber] = useState(0)

     function addCart(id){
        
        let body  = {
            productId:id
        }
        let headersOptions = {
            headers:{
                token:localStorage.getItem("userToken")
            }
        }
        return  axios.post('https://ecommerce.routemisr.com/api/v1/cart' , body , headersOptions)
    }
//////
    function getUserCart(){
        let headersOptions = {
            headers:{
                token:localStorage.getItem("userToken")
            }
        }
        return axios.get(`https://ecommerce.routemisr.com/api/v1/cart`,headersOptions) 
    }
//////
    function removeCart(id){
        let headersOptions = {
            headers:{
                token:localStorage.getItem("userToken")
            }
        }
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${id}`,headersOptions) 
    }
//////
    function clearCart(){
        let headersOptions = {
            headers:{
                token:localStorage.getItem("userToken")
            }
        }
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart`,headersOptions) 
    }
//////
    function updateCart(id,count){
        let headersOptions = {
            headers:{
                token:localStorage.getItem("userToken")
            }
        }
        let body = {
            count
        }
        return axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${id}`,body,headersOptions) 
    }
//////
    function checkoutPayment(id,data){
        let headersOptions = {
            headers:{
                token:localStorage.getItem("userToken")
            }
        }
        let body  = {
            shippingAddress:data
        }
        return axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${id}?url=http://localhost:3000`,body,headersOptions)
    }

    return <CartContext.Provider value={{checkoutPayment,updateCart,clearCart,removeCart,getUserCart,addCart,numsItem,setItemNumber}}>
        {children}
    </CartContext.Provider>
}