import axios from 'axios';
import { useFormik } from 'formik'
import React, { useContext, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import * as Yup from "yup"
import { UserContext } from '../Context/UserContext';
import { CartContext } from '../Context/CartContext';


export default function Login() {
  let {getUserCart,setItemNumber} = useContext(CartContext)
  let {setToken} = useContext(UserContext)
  let navg = useNavigate()
let [errMessage,SetErrMessage] = useState("")
let [loading,setLoading] = useState(true)
let validationSchema = Yup.object({
  email:Yup.string().required("Email is Required").email("Enter Valid Email"),
  password:Yup.string().required('Password is Required').matches(/^[A-Z][a-zA-Z!@#$%^&*(_)]{6,16}$/,"Enter valid password"),
})

  let formLogin = useFormik({
    initialValues: {
      email: '',
      password: '',
    },
    onSubmit: LoginForm,
    validationSchema,
    
  })

  async function LoginForm(value) {
    setLoading(false)
    let req =await axios.post("https://ecommerce.routemisr.com/api/v1/auth/signin",
    value).catch(function(err){
      SetErrMessage(err.response.data.message)
      setLoading(true)    
      console.log(err.response.data.message);
    })
    if(req?.data.message == "success"){
      setLoading(true)
      localStorage.setItem("userToken" ,req.data.token)
      setToken(req.data.token)  
      getUserCartData()  
      navg('/home')
    }
  }
  async function getUserCartData(){
    let req = await getUserCart().catch((err)=>{
      console.log(err);
    })
    if(req?.data?.status == "success"){
      setItemNumber(req.data.numOfCartItems)
    }
  }

  function forgetNavg(){
    navg('/forgetPassword')
  }
  return (
    <div>
      <h2>Login Now....</h2>
      {errMessage != "" ?
      <div className="alert alert-danger">{errMessage}</div> : ""}
      <form onSubmit={formLogin.handleSubmit} action="">


        <div className="">
          <label htmlFor="email">Email:</label>
          <input onChange={formLogin.handleChange} onBlur={formLogin.handleBlur} className='form-control mb-3' name="email" type="text" id="email" />
            
          {(formLogin.errors.email && formLogin.touched.email) ? <div className="alert alert-danger">
          {formLogin.errors.email}
          </div> : ""}

        </div>

        <div className="">
          <label htmlFor="password">Password:</label>
          <input onChange={formLogin.handleChange} onBlur={formLogin.handleBlur} className='form-control mb-3' name="password" type="password" id="password" />
            
          {(formLogin.errors.password && formLogin.touched.password) ? <div className="alert alert-danger">
          {formLogin.errors.password}
          </div> : ""}

        </div>
        <div className="">
          <span onClick={forgetNavg} className='cursor-pointer  mt-2 text-muted'>Forget Password....??</span>
        </div>

        {loading ?<button disabled={!(formLogin.isValid && formLogin.dirty)} className='btn bg-main text-white mt-2' type='submit'>Login</button>
        :<button className='btn bg-main text-white mt-2' type='button'>
        <i className='fa-solid fa-spinner fa-spin'></i>
        </button>}

      </form>
    </div>
  )
}
