import axios from "axios"
import { useContext, useEffect, useState } from "react";
import Spinner from "../Spinner/Spinner";
import { WishContext } from "../WishContext/WishContext";
import { CartContext } from "../Context/CartContext";
import Swal from 'sweetalert2'


export default function WishList() {
        let {addCart,setItemNumber} = useContext(CartContext)
        let {getwishListProducts,removeWish,setWishNum,setWishId} = useContext(WishContext)
        let [loading,setloading] = useState(true)
        let [wishData,setwishData] = useState(null)
        useEffect(()=>{
          getwishList()
        },[])
        
        async function getwishList(){
          setloading(true)
          let req = await getwishListProducts().catch((err)=>{
            if(err?.response?.data.statusMsg == "fail"){
              setwishData(null)
              setloading(true)
            }
          })
          if(req?.data.status == "success");{
            setwishData(req?.data?.data)
            setWishNum(req?.data.count)
            setloading(false)
          }
        }

        async function AddToCart(id) {
          let req = await addCart(id).catch((err) => {
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "Something went wrong!",
              footer: '<a href="#">Why do I have this issue?</a>'
            });
      
          })
          if (req.data.status == 'success') {
            setItemNumber(req.data.numOfCartItems)
            Swal.fire({
              title: "Good job!",
              text: "You clicked the button!",
              icon: "success"
            });
          }
        }

        async function removewishItem(id){
          let req = await removeWish(id).catch()
          if(req?.data.status == "success"){
            getwishList()
          }
        }
      
    return (
       <>
       {loading ? <Spinner/>:
       <>
        {wishData == null ? <div className="alert alert-danger text-white">WishList is Empty</div> :
       <>
       {wishData.map((e)=>{
        return <div className="row py-3 border-bottom border-1 align-items-center">
        <div className="col-md-10">
          <div className="row align-items-center">
            <div className="col-md-1">
              <img src={e.imageCover} className='w-100' alt="" />
            </div>
            <div className="col-md-10">
              <h6></h6>
              <h5 className='text-muted'>price:{e.price}$</h5>
              <button onClick={()=>removewishItem(e._id)} className='btn btn-danger btn-sm'>Remove <i className='fa-solid fa-trash'></i></button>
            </div>
          </div>
        </div>


        <div className="col-md-2">
         <button onClick={()=>AddToCart(e._id)} className="btn bg-main text-white">Add Product</button>
        </div>
      </div>   
      })}     
       </>
       }
       </>
       }
    </>
    )
  }
  