import React, { useContext, useEffect } from 'react'
import { RouterProvider, createBrowserRouter, createHashRouter } from 'react-router-dom'
import Home from './Component/Home/Home'
import Layout from './Component/Layout/Layout'
import Products from './Component/Products/Products'
import Categories from './Component/Categories/Categories'
import Login from './Component/Login/Login'
import NotFound from './Component/NotFound/NotFound'
import Register from './Component/Register/Register'
import { UserContext, UserContextProvider } from './Component/Context/UserContext'
import GuardRouting from './Component/ProtectedRouting/GuardRouting'
import ForgetPassword from './Component/ForgetPassword/ForgetPassword'
import ResetPassword from './Component/ResetPassword/ResetPassword'
import {QueryClient,QueryClientProvider} from 'react-query'
import {ReactQueryDevtools} from 'react-query/devtools'
import ProductDetails from './Component/ProductDetails/ProductDetails'
import Cart from './Component/Cart/Cart'
import { CartContextprovider } from './Component/Context/CartContext'
import Checkout from './Component/Checkout/Checkout'
import WishList from './Component/WishList/WishList'
import { WishContextprovider } from './Component/WishContext/WishContext'
import Brands from './Component/Brands/Brands'

export default function App() {
  let QueryClients = new QueryClient()


  let routes = createHashRouter([
    {path:'',
    element: <Layout />,
     children: [
     {path:"home",element:<GuardRouting><Home /></GuardRouting>} ,
     {path:"products",element:<GuardRouting><Products /></GuardRouting>} ,
     {path:"wishlist",element:<GuardRouting><WishList /></GuardRouting>} ,
     {path:"categories",element:<GuardRouting><Categories /></GuardRouting>} ,
     {path:"brands",element:<GuardRouting><Brands /></GuardRouting>} ,
     {path:"checkout/:id",element:<GuardRouting><Checkout /></GuardRouting>} ,
     {path:"productsDetails/:id",element:<GuardRouting><ProductDetails /></GuardRouting>} ,
     {path:"cart",element:<GuardRouting><Cart /></GuardRouting>} ,
     {path:"login",element:<Login/>} ,
     {path:"forgetPassword",element:<ForgetPassword/>} ,
     {path:"ResetPassword",element:<ResetPassword/>} ,
     {index:true,element:<Register />} ,
     {path:"*",element:<NotFound />} ,
    ]}
  ])

  let {setToken} = useContext(UserContext)
  useEffect(()=>{
    if(localStorage.getItem("userToken") != null){
      setToken(localStorage.getItem("userToken"))
    }
  },[])
  return (
   <>
   <QueryClientProvider client={QueryClients}>
   <CartContextprovider>
    <WishContextprovider>
   <RouterProvider router={routes}></RouterProvider>
   </WishContextprovider>
   </CartContextprovider>
   </QueryClientProvider>
   </>
  )
}
