import React, { useContext, useEffect, useState } from 'react'
import { Link, NavLink, useNavigate } from 'react-router-dom'
import logo from '../../assets/images/freshcart-logo.svg'
import { UserContext } from '../Context/UserContext'
import { jwtDecode } from 'jwt-decode';
import { CartContext } from '../Context/CartContext';
import axios from 'axios';
import { WishContext } from '../WishContext/WishContext';

export default function Navbar() {
  let {wishNum} = useContext(WishContext)
  let {numsItem} = useContext(CartContext)
  let {userToken,data,setToken} = useContext(UserContext)
  let navg = useNavigate()
   function Logout(){
    localStorage.removeItem("userToken")
    setToken(null)
    navg("/login")
   }
  return (
   <>
   <nav className="navbar navbar-expand-lg bg-body-tertiary">
  <div className="container-fluid">
    <Link className="navbar-brand" to="home"><img src={logo} alt="" /></Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">

    {data != null ?  <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <NavLink className={(activeLink)=>activeLink.isActive == true ? "isActive nav-link" : "nav-link"} to="home">Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className={(activeLink)=>activeLink.isActive == true ? "isActive nav-link" : "nav-link"} to="categories">Category</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className={(activeLink)=>activeLink.isActive == true ? "isActive nav-link" : "nav-link"} to="cart">Cart</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className={(activeLink)=>activeLink.isActive == true ? "isActive nav-link" : "nav-link"} to="brands">Brands</NavLink>
        </li>
      </ul> : ""}
     


      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        <li className="nav-item d-flex align-items-center">
          <i className='fa-brands fa-facebook mx-2'></i>
          <i className='fa-brands fa-twitter mx-2'></i>
          <i className='fa-brands fa-instagram mx-2'></i>
          <i className='fa-brands fa-youtube mx-2'></i>
          <i className='fa-brands fa-pinterest mx-2'></i>
        </li>

        {data != null ?<li className="nav-item d-flex align-items-center">


        <NavLink to="/wishlist">
          <div className="position-relative">
          <i className='fa-regular fa-heart nav-link text-main'></i>
          <span className='position-absolute top-0 translate-middle-y end-0'>{wishNum}</span>
          </div>
          </NavLink>
          
          <NavLink to='/cart'>
          <div className=" position-relative">
          <i className='fa-solid fa-cart-shopping nav-link text-main'></i>
          <span className='position-absolute top-0 translate-middle-y end-0'>{numsItem}</span>
          </div>
          </NavLink>

          <span className="nav-link d-inline isActive">{data?.name}</span>
          <span className="nav-link cursor-pointer d-inline" onClick={Logout}>logOut</span>
        </li> : <><li className="nav-item">
          <NavLink className={(activeLink)=>activeLink.isActive == true ? "isActive nav-link" : "nav-link"} to="login">login</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className={(activeLink)=>activeLink.isActive == true ? "isActive nav-link" : "nav-link"} to="/">register</NavLink>
        </li></>}
      </ul>
    </div>
  </div>
</nav>
   </>
  )
}
