import axios from 'axios';
import { useFormik } from 'formik'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import * as Yup from "yup"


export default function Register() {
  let navg = useNavigate()
let [errMessage,SetErrMessage] = useState("")
let [laoding,setLoading] = useState(true)
let validationSchema = Yup.object({
  name:Yup.string().required("Name is Required").min(3,"min char 3").max(20,"max char 20"),
  email:Yup.string().required("Email is Required").email("Enter Valid Email"),
  password:Yup.string().required('Password is Required').matches(/^[A-z][a-zA-z!@#$%^&*(_)]{6,16}$/,"Enter valid password"),
  rePassword:Yup.string().required("Repassword is Required").oneOf([Yup.ref("password")],"Enter matched Password"),
  phone:Yup.string().required("Phone is Required").matches(/^01[1250][0-9]{8}$/,"Enter Valid Phone"),
})

  let formRegister = useFormik({
    initialValues: {
      name: '',
      phone: '',
      email: '',
      password: '',
      rePassword: '',
    },
    onSubmit: RegisterForm,
    validationSchema,
    
  })

  async function RegisterForm(value) {
    setLoading(false)
    let req =await axios.post("https://ecommerce.routemisr.com/api/v1/auth/signup",
    value).catch(function(err){
      SetErrMessage(err.response.data.message)
      setLoading(true)    
      console.log(err.response.data.message);
    })
    if(req?.data.message == "success"){
      navg('/login')
      setLoading(true)    
    }
  }

  return (
    <div>
      <h2>Register Now....</h2>
      {errMessage != "" ?
      <div className="alert alert-danger">{errMessage}</div> : ""}
      <form onSubmit={formRegister.handleSubmit} action="">

        <div className="">
          <label htmlFor="name">Name:</label>
          <input onChange={formRegister.handleChange} onBlur={formRegister.handleBlur} className='form-control mb-3' name="name" type="text" id="name" />

          {(formRegister.errors.name && formRegister.touched.name) ? <div className="alert alert-danger">
          {formRegister.errors.name}
          </div> : ""}

        </div>

        <div className="">
          <label htmlFor="email">Email:</label>
          <input onChange={formRegister.handleChange} onBlur={formRegister.handleBlur} className='form-control mb-3' name="email" type="text" id="email" />
            
          {(formRegister.errors.email && formRegister.touched.email) ? <div className="alert alert-danger">
          {formRegister.errors.email}
          </div> : ""}

        </div>

        <div className="">
        <label htmlFor="phone">Phone:</label>
        <input onChange={formRegister.handleChange} onBlur={formRegister.handleBlur} className='form-control mb-3' name="phone" type="tel" id="phone" />

        {(formRegister.errors.phone && formRegister.touched.phone) ? <div className="alert alert-danger">
          {formRegister.errors.phone}
          </div> : ""}

        </div>

        <div className="">
          <label htmlFor="password">Password:</label>
          <input onChange={formRegister.handleChange} onBlur={formRegister.handleBlur} className='form-control mb-3' name="password" type="password" id="password" />
            
          {(formRegister.errors.password && formRegister.touched.password) ? <div className="alert alert-danger">
          {formRegister.errors.password}
          </div> : ""}

        </div>

        <div className="">
          <label htmlFor="rePassword">rePassword:</label>
          <input onChange={formRegister.handleChange} onBlur={formRegister.handleBlur} className='form-control mb-3' name="rePassword" type="password" id="rePassword" />

          {(formRegister.errors.rePassword && formRegister.touched.rePassword) ? <div className="alert alert-danger">
          {formRegister.errors.rePassword}
          </div> : ""}

        </div>

        {laoding ?<button disabled={!(formRegister.isValid && formRegister.dirty)} className='btn bg-main text-white' type='submit'>Register</button>
        :<button className='btn bg-main text-white' type='button'>
        <i className='fa-solid fa-spinner fa-spin'></i>
        </button>}

      </form>
    </div>
  )
}
