import React, { useContext, useEffect } from 'react'
import Navbar from '../Navbar/Navbar'
import {Outlet, useNavigate} from 'react-router-dom'
import { CartContext } from '../Context/CartContext'
import { Offline } from 'react-detect-offline'
import { WishContext } from '../WishContext/WishContext'

export default function Layout() {
  let {getUserCart,setItemNumber} = useContext(CartContext)
  let {getwishListProducts,setWishNum,setWishId,wishId} = useContext(WishContext)
  useEffect(()=>{
    if(localStorage.getItem("userToken") != null){
      getUserCartData()
      getwishList()
    }
  },[])
  async function getUserCartData(){
    let req = await getUserCart().catch((err)=>{
      console.log(err);
    })
    if(req?.data.status == "success"){
      setItemNumber(req?.data?.numOfCartItems)
    }
  }

  async function getwishList(){
    let req = await getwishListProducts().catch((err)=>{
      if(err?.response?.data.statusMsg == "fail"){
        console.log(err);
      }
    })
    if(req?.data.status == "success");{
      setWishNum(req?.data?.count)
    }
  }
  return (
    <div>
      <div className="offline">
        <Offline>Only shown offline (surprise!)</Offline>
      </div>
      <Navbar />
      <div className="container pt-5">
      <Outlet />
      </div>
    </div>
  )
}
