import axios from 'axios'
import React, { useEffect, useState } from 'react'
import Spinner from '../Spinner/Spinner'

export default function Categories() {
  let [loading,setloading] = useState(true)
  let [categoriesList , setcategoriesList] = useState([])
  let [categoriesDetails , setcategoriesDetails] = useState([])
  let [sub , setsub] = useState([])
  useEffect(()=>{
    getCategories()
  },[])
  async function getCategories(){
    let req = await axios.get(`https://ecommerce.routemisr.com/api/v1/categories`)
    if(req?.status == 200){
      setcategoriesList(req?.data?.data)
      setloading(false)
    }
  }
  
  async function getCategoriesDetails(id){
    let req = await axios.get(`https://ecommerce.routemisr.com/api/v1/categories/${id}`)
    if(req?.status == 200){
    }
    setcategoriesDetails(req?.data?.data)
    console.log(req?.data?.data);
  }
  
  async function getSubCategory(id){
    let req = await axios.get(`https://ecommerce.routemisr.com/api/v1/categories/${id}/subcategories`)
    if(req?.status == 200){
    }
    setsub(req?.data?.data)
    console.log(req?.data?.data);
  }

  return (
    <div>
     {loading ? <Spinner/> :
     <>
      <div className="row g-4">
      {categoriesList.map((e)=>{
            return <div onClick={()=>{getCategoriesDetails(e._id); getSubCategory(e._id) }} className="col-md-4">
            <div className="card Brand">
              <div className="cardImg w-100 overflow-hidden">
              <img className='w-100 img-fluid ratio-4x3' src={e.image}  alt="..." />
              </div>
              <div className="card-body">
                <h3 className="card-text text-success fw-bold d-flex justify-content-center">{e.name}</h3>
              </div>
            </div>
          </div>
        })}
      </div>
        
        <h3 className="card-text text-success fw-bold d-flex justify-content-center my-5">{categoriesDetails.name}</h3>
      <div className="row g-4">
        {sub.map((e)=>{
          return<>
          <div className="col-md-4">
          <div className="border border-black text-center Brand">
            <h3 className='my-3 fw-bold'>{e.name}</h3>
          </div>
        </div>
          </> 
        })}
      </div>
     </>
     }
    </div>
  )
}
