import axios from "axios";
import { createContext, useState } from "react";


export let WishContext = createContext()
export function WishContextprovider({ children }) {
    let [wishNum,setWishNum]= useState(0)
    function PostWish(id) {
        let headersOptions = {
            headers: {
                token: localStorage.getItem("userToken")
            }
        }
        let body = {
            productId: id
        }

        return axios.post(`https://ecommerce.routemisr.com/api/v1/wishlist`, body , headersOptions)
    }


     function getwishListProducts(){
        let headersOptions = {
          headers: {
              token: localStorage.getItem("userToken")
          }
      }
       return axios.get(`https://ecommerce.routemisr.com/api/v1/wishlist`,headersOptions)
    }

    function removeWish(id){
        let headersOptions = {
            headers:{
                token:localStorage.getItem("userToken")
            }
        }
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/wishlist/${id}`,headersOptions) 
    }
    return <WishContext.Provider value={{PostWish,getwishListProducts,wishNum,setWishNum,removeWish}}>
        {children}
    </WishContext.Provider>
}