import React, { useState } from 'react'
import { useFormik } from 'formik'
import * as Yup from "yup"
import axios from 'axios'
import { useNavigate } from 'react-router-dom'


  export default function ResetPassword() {
    let navg = useNavigate()
    let [errMessage,SetErrMessage] = useState("")
    let validationSchema = Yup.object({
        email:Yup.string().required("Email is Required").email("Enter Valid Email"),
        newPassword:Yup.string().required('newPassword is Required').matches(/^[A-Z][a-zA-z!@#$%^&*(_)]{6,16}$/,"Enter valid password"),
      })
    let resetFormik = useFormik({
        initialValues:{
            email:"",
            newPassword:""
        },
        onSubmit:ResetPasswordApi ,
        validationSchema,
    })

    async function ResetPasswordApi(val){
        let req = await axios.put(`https://ecommerce.routemisr.com/api/v1/auth/resetPassword`,val)
        if(req.data.token){
            navg("/login")
        }
        console.log(req);
    }
  return (
    <div>
      <h2>Reset Password</h2>
      <form onSubmit={resetFormik.handleSubmit} action="">


        <div className="">
          <label htmlFor="email">Email:</label>
          <input onChange={resetFormik.handleChange} onBlur={resetFormik.handleBlur} className='form-control mb-3' name="email" type="text" id="email" />
            
          {(resetFormik.errors.email && resetFormik.touched.email) ? <div className="alert alert-danger">
          {resetFormik.errors.email}
          </div> : ""}

        </div>

        <div className="">
          <label htmlFor="newPassword">newPassword:</label>
          <input onChange={resetFormik.handleChange} onBlur={resetFormik.handleBlur} className='form-control mb-3' name="newPassword" type="password" id="newPassword" />
            
          {(resetFormik.errors.newPassword && resetFormik.touched.newPassword) ? <div className="alert alert-danger">
          {resetFormik.errors.newPassword}
          </div> : ""}

        </div>

        <button  className='btn bg-main text-white mt-2' type='submit'>Update Password</button>

      </form>
    </div>
  )
}
