import { queries } from '@testing-library/react'
import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { useQuery } from 'react-query'
import { useParams } from 'react-router-dom'
import Spinner from '../Spinner/Spinner'
import { CartContext } from '../Context/CartContext'
import Swal from 'sweetalert2'

export default function ProductDetails() {
  let { addCart, setItemNumber } = useContext(CartContext);
  let pramas = useParams();
  let [productId, setProductId] = useState();
  useEffect(() => {
    setProductId(pramas.id);
  }, []);
  let { data, isLoading } = useQuery(["productDetails", productId], getDetails);
  let product = data?.data.data;

  console.log(data?.data.data.imageCover);
  function getDetails(query) {
    return axios.get(
      `https://ecommerce.routemisr.com/api/v1/products/${query.queryKey[1]}`
    );
  }

  function getSrc(e) {
    let imgPath = e.target.getAttribute("src");
    document.querySelector("#myImage")?.setAttribute("src", imgPath);
  }

  async function AddToCart(id) {
    let req = await addCart(id).catch((err) => {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: "Something went wrong!",
        footer: '<a href="#">Why do I have this issue?</a>'
      });
    });
    console.log(req);
    if (req.data.status == "success") {
      setItemNumber(req.data.numOfCartItems);
      Swal.fire({
        title: "Good job!",
        text: "You clicked the button!",
        icon: "success",
      });
    }
  }

  return (
    <>
      {isLoading ? (
        <Spinner />
      ) : (
        <div className="container py-5">
          <div className="row align-items-center">
            <div className="col-md-3">
              <div className="row g-0 align-items-center">
                <div className="col-md-2">
                  {data?.data.data.images.map((el) => {
                    return (
                      <img
                        src={el}
                        onClick={getSrc}
                        className="w-100 imgs"
                        alt=""
                      />
                    );
                  })}
                </div>
                <div className="col-md-10">
                  <img
                    src={data?.data.data.imageCover}
                    id="myImage"
                    className="w-100"
                    alt=""
                  />
                </div>
              </div>
            </div>
            <div className="col-md-9">
              <h2>{data?.data.data.title}</h2>
              <p className="text-muted my-3">{data?.data.data.description}</p>
              <h6 className="main-color">{data?.data.data.category.name}</h6>
              <div className="d-flex justify-content-between">
                <span>{data?.data.data.price}EGP</span>
                <span>
                  <i className="fa-solid fa-star rating-color"></i>
                  {data?.data.data.ratingsAverage}
                </span>
              </div>
              <button
                className="btn bg-main text-white w-100 d-block my-3"
                onClick={() => {
                  AddToCart(data?.data.data.id);
                }}
              >
                Add To Cart
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
}