import React from 'react'

export default function Spinner() {
  return (
    <div>
      <div className="loading d-flex justify-content-center align-items-center bg-white position-fixed top-0 end-0 bottom-0 start-0 z-3">
    <span className="loader z-3"></span>
    </div>
    </div>
  )
}
