import React, { useContext, useEffect, useState } from 'react'
import { CartContext } from '../Context/CartContext'
import Spinner from '../Spinner/Spinner'
import { Link } from 'react-router-dom'
import { logDOM } from '@testing-library/react'

export default function Cart() {
  
  let {getUserCart,removeCart,clearCart,setItemNumber,updateCart} = useContext(CartContext)
  let [cartData,setCartData] = useState(null)
  let [loading,setLoading] = useState(true)
  useEffect(()=>{
    getUserCartData()
  },[])
  async function getUserCartData(){
    setLoading(true)
    let req = await getUserCart().catch((err)=>{
      if(err?.response?.data?.statusMsg == "fail"){
        setLoading(false)
        setCartData(null)
      }
    })
    if(req?.data.status == 'success'){
      setLoading(false)
      setCartData(req?.data?.data)
    }
  }
  async function removeItemCart(id){
    let req = await removeCart(id).catch()
    if(req?.data.status == "success"){
      setItemNumber(req?.data.numOfCartItems)
      setCartData(req.data.data)
    }
  }
  async function clearCartData(){
    let req = await clearCart()
    if(req?.data.message == "success"){
      setCartData(null)
      setItemNumber(0)
    }
  }
  async function updateCartItem(id,count){
    if(count == 0){
      removeItemCart(id)
    }else{
      let req = await updateCart(id,count).catch()
      if(req?.data.status == "success"){
        setCartData(req.data.data)
      }
    }
  }
  return (
    <>
    {loading ? <Spinner/>:
    <>
    {cartData == null ? <div className="alert alert-danger">Cart Empty</div> :
       <div className="container bg-body-tertiary">
      
      <button onClick={clearCartData} className='btn btn-danger btn-sm float-end'>Clear Cart</button>
      <div className="clearfix"></div>

        {cartData.products.map((e)=>{
          return <div className="row py-3 border-bottom border-1 align-items-center">
          <div className="col-md-10">
            <div className="row align-items-center">
              <div className="col-md-1">
                <img src={e.product.imageCover} className='w-100' alt="" />
              </div>
              <div className="col-md-10">
                <h6>{e.product.title}</h6>
                <h5 className='text-muted'>price:{e.price}EGP</h5>
                <button onClick={()=>removeItemCart(e.product._id)} className='btn btn-danger btn-sm'>Remove <i className='fa-solid fa-trash'></i></button>
              </div>
            </div>
          </div>


          <div className="col-md-2">
            <span onClick={()=>updateCartItem(e.product._id,e.count += 1)} className='btn btn-success btn-sm'>
              <i className='fa-solid fa-plus'></i>
            </span>
            <span className='mx-2'>
             {e.count} 
            </span>
            <span onClick={()=>updateCartItem(e.product._id,e.count -= 1)}  className='btn btn-danger btn-sm'>
              <i className='fa-solid fa-minus'></i>
            </span>
          </div>
        </div>
        })}
        <h3 className='text-main'>Total Price:{cartData.totalCartPrice}EGP</h3>
        <Link to={'/checkout/'+cartData._id} className='btn bg-main text-white'>Check Out</Link>
      </div>}
    </>
    }
      
    </>
  )
}
